/* src/App.css */
:root {
  --primary-color: #61dafb;
  --bg-color: #121212;
  --text-color: #ffffff;
}

.App {
  background-color: var(--bg-color);
  color: var(--text-color);
  min-height: 100vh;
  font-family: sans-serif;
  scroll-behavior: smooth;
}

/* ÏÉÅÎã® Ìó§Îçî Ïä§ÌÉÄÏùº */
.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;
  background-color: rgba(18, 18, 18, 0.9);
  position: sticky;
  top: 0;
  z-index: 1000;
  border-bottom: 1px solid #333;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 30px;
}

.nav-links a {
  text-decoration: none;
  color: white;
}

/* ÏÑπÏÖò Í≥µÌÜµ Ïä§ÌÉÄÏùº */
section {
  padding: 80px 20px;
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  font-size: 2.5rem;
  color: var(--primary-color);
  border-bottom: 2px solid var(--primary-color);
  display: inline-block;
  margin-bottom: 30px;
}

/* üìä ÎèÖÏÑú Î™©Ìëú Í≤åÏù¥ÏßÄ Ïä§ÌÉÄÏùº (Ïä§Ïª¨!) */
.progress-container {
  background-color: #333;
  border-radius: 20px;
  margin: 20px 0;
  height: 35px;
  width: 100%;
  overflow: hidden;
}

.progress-bar {
  background: linear-gradient(90deg, #61dafb, #4facfe);
  height: 100%;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #000;
  box-shadow: 0 0 15px rgba(97, 218, 251, 0.5);
  transition: width 1.5s ease-out;
}